.fox_backgroud_slider {
  width: 100% !important;

  * img {
    object-fit: cover !important;

  }

  .slider_wrapper {
    width: 100% !important;
    position: relative;

    .slider_inside {
      width: 100% !important;
      height: 100% !important;
      position: relative;

      img {
        position: absolute !important;
        left: 0;
        top: 0;
        width: 100% !important;
        height: 100% !important;
        transition: all 8s !important;
        opacity: 0
      }
    }
  }
}