.fox-galery {
  * img {
    object-fit: cover !important;
  }
}

.box_gallery {
  width: 100% !important;
  display: flex;

  // flex-wrap: wrap;
  .group_colum {
    // display: flex;
    // flex-wrap: wrap;
    width: 33.33%;

    .img_galery {
      width: 100%;
      padding: 5px;

      img {

        width: 100%;
        height: auto;
        object-fit: contain !important;
      }
    }
  }
}

.zoom_posizion {
  position: fixed;
  z-index: 9999999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, .4);

  .zoom_box {
    position: relative;
    display: flex;
    width: 100%;
    padding: 20px;

    .button_close {
      position: absolute;
      right: 5px;
      z-index: 9;
      top: 5px;
      background-color: #ffffff;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      opacity: .7;

      &:hover {
        opacity: 1;
      }
    }

    .zoom_container {
      width: 100%;
      position: relative;

      // transition: all .5s;
      animation: zoom_out .5s;

      // transform: scale(.6);
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        object-fit: contain !important;
      }
    }
  }
}

@keyframes zoom_out {
  0% {
    transform: scale(.6);
  }

  100% {
    transform: scale(1);
  }
}