.fox_backgroud_slider_side {
  width: 100% !important;

  * img {
    object-fit: cover !important;
  }

  .slider_wrapper {
    width: 100% !important;
    position: relative;
    height: 150px;
    .slider_inside {
      // width: 100% !important;
      height: 100% !important;
      position: relative;
      display: flex;
      transition: all 1s !important;
      .image_box {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        img {
          // position: absolute !important;
          // left: 0;
          // top: 0;
          width: 100% !important;
          height: 100% !important;

          // opacity: 0;
        }
      }
    }
  }
}

.w-full {
  width: 100% !important;
}

.h-full {
  height: 100% !important;
}

.slider_galery_box {
  width: 100%;
  height: 0;
  padding-top: 64%;
  position: relative;
  margin-bottom: 10px;
  & > .swiper {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
  }
}

.swiper-button-prev,
.swiper-button-next {
  svg {
    width: 30px;
    height: 30px;
    fill: #fff;
  }
}
