.muncul {
  position: relative;
  // transform: translateY(6rem) scale(0.93);
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}
.muncul.active {
  transform: translateY(0px);
  opacity: 1;
}
.muncul-kiri {
  position: relative;
  // transform: translateX(-100%) scale(0.93);
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}
.muncul-kiri.active {
  transform: translateX(0px);
  opacity: 1;
}
.muncul-kanan {
  position: relative;
  transform: translateX(100%) scale(0.93);
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}
.muncul-kanan.active {
  transform: translateX(0px);
  opacity: 1;
}

.animated_hide {
  &:not(.img_galery) {
    position: relative;
    // transform: translateY(6rem) scale(0.93);
    opacity: 0;
    transition: opacity 0.5s, transform 1s;
  }
}
.animated {
  position: relative;
  // &.animated_hide:not(.img_galery) {
  //   transform: translateY(1px) !important;
  // }
  &.left_animated {
    transform: translateX(0px) !important;
    opacity: 1;
  }
  &.right_animated {
    transform: translateX(0px) !important;
    opacity: 1;
  }
}
.left_animated {
  position: relative;
  transform: translateX(-100%) scale(0.93) !important;
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}

.right_animated {
  position: relative;
  transform: translateX(100%) scale(0.93) !important;
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}

