.lazyfox_input_file{
  margin-top: 20px;
  width: 100%;
  // background-color: #fff;
  position: relative;
  .value_title{
    font-size: 12px;
    padding: 0 10px 0 5px;
    margin: 0;
    display: block;
    font-weight: 500;
    letter-spacing: 1px;
    position: absolute;
    // bottom: -8px;
    background-color: inherit;
    color: #757575;
    font-weight: 700;
    margin-top: -6px;
    z-index: 6;
    background-color: #fff;
  }
  .file-name{
    padding: 10px;
    box-shadow: 0 0 4px rgba(3,3,3,.3);
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .details{
      display: flex;
      align-items: center;
    }
    .progress-icon{
      &.done{
        fill: var(--notify-passed);
      }
      &.loading{
        animation: animLoader .5s infinite;
        fill: var(--niotify-warning);
      }
      &.error{
        fill: var(--notify-alert);
      }
    }
  }
  .lazyfox_select_box_value, .lazyfox_input_box_value{
    position: relative;
    //border: 1px solid #d4d4d4;
    box-shadow: 0 0 4px rgba(3,3,3,.3);
    border-radius: 3px;
    .is_error{
      box-shadow: $notifyAlertShadow;
    }
    .value_box{
      padding: 12px 15px;
      font-weight: 500;
      // opacity: .7;
      min-height: 100px;
      @extend .d_flex;
      @extend .align_items_center;
      align-items: center;
      justify-content: center;
      .value_text_placeholder{
        color: $notActive;
        opacity: .7;
        cursor: pointer;
      }
      cursor: pointer;
      .input_box_values{
        @extend .value_text_placeholder;
        border: none;
        outline: none;
        font-weight: 500;
        width: 100%;
        background-color: red;
        background-color: transparent;
        opacity: 1;
        color: #494949;
        resize: none;
        cursor: pointer;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
        opacity: 0;
      }
      .button-browse{
        position: absolute;
        span{
          margin-left: 10px
        }
      }
    }
  }
  .error_wrappers{
    @extend .d_flex;
    @extend .align_items_center;
    margin-top: 5px;
    .icons_error{
      fill: $notifyAlert;
    }

    .error_text{
      color: $notifyAlert;
      display: block;
      padding: 5px 0;
      margin-left: 10px;
      font-size: 13px;
    }
  }
  .lazyfox_select_box_list{
    padding: 10px;
  }
  
}

@keyframes animLoader{
  0%{
      transform: rotate(0deg);
  }
  100%{
      transform: rotate(360deg);
  }
}


